<template lang="en">
    <svg
        :width="radius * 2"
        :height="radius * 2">

            <circle
            class="progress-ring__circle"
            stroke="#bcbcbc"
            fill="transparent"
            :style="{ strokeDashoffset: strokeDashoffset }"
            :stroke-width="stroke"
            :r="normalizedRadius"
            :cx="radius"
            :cy="radius"/>
        

        <circle
            class="progress-ring_circle-fill"
            fill="transparent"
            stroke-linecap="round"
            :stroke-dasharray="circumference + ' ' + circumference"
            :style="{ strokeDashoffset: strokeDashoffset }"
            :stroke-width="stroke"
            :r="normalizedRadius"
            :cx="radius"
            :cy="radius"/>
    </svg>
</template>

<script>
export default {
  props: {
    radius: String,
    progress: Number,
    stroke: String
  },
  data() {
    const normalizedRadius = this.radius - this.stroke * 2;
    const circumference = normalizedRadius * 2 * Math.PI;

    return {
      normalizedRadius,
      circumference
    };
  },
  computed: {
    strokeDashoffset() {
      return this.circumference - (this.progress / 100) * this.circumference;
    }
  }
};
</script>

<style lang="en"></style>
