<template lang="en">
<div id="skills" class="row section">
<div class="col">
  <h2>Skills</h2>
     <ol class="skills">
        <li v-for="skill in skills" v-html="skill.description" class="skills__item"></li>
    </ol>
    </div>
    </div>
</template>
<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      skills: []
    };
  },
  methods: {
    getSkills() {
      Utilities.sendRequest("skills").then(response => {
        this.skills = response;
      });
    }
  },
  created() {
    this.getSkills();
  }
};
</script>
<style lang="en"></style>
