<template lang="en">
<div class="col">
  <p><strong>Hi! I'm Arturo Casas</strong></p>
  <p v-html="description"></p>
  </div>
</template>
<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      description: ""
    };
  },
  methods: {
    getAboutMe() {
      Utilities.sendRequest("aboutme").then(response => {
        this.description = response.description;
      });
    }
  },
  created() {
    this.getAboutMe();
  }
};
</script>
<style lang="en" scope></style>
