<template lang="en">
    <div class="col">
      <h2>Contact me</h2>
        <form class="contact" v-on:submit.prevent="sendForm">
            <div class="contact__element">
                <label for="ipName" class="contact__element--label">Your name</label>
                <input type="text" class="contact__element--input" v-model="form.name" required />
            </div>
            <div class="contact__element">
                <label for="ipEmail">Your E-mail</label>
                <input type="email" class="contact__element--input" v-model="form.email" required />
            </div>
            <div class="contact__element">
                <label for="ipSubject">Subject</label>
                <input type="text" class="contact__element--input" v-model="form.subject" required />
            </div>
            <div class="contact__element">
                <label for="ipComment">Your comment</label>
                <textarea type="text" class="contact__element--input" v-model="form.comment" required rows="4" cols="50"></textarea>
            </div>
            <div class="contact__element">
                <button type="submit" class="contact__element--button primary-button">Send</button>
            </div>
        </form>
    </div>
</template>

<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      form: {
        name: null,
        email: null,
        subject: null,
        comment: null
      }
    };
  },
  methods: {
    isFormValid() {
      let fieldValue,
        countFields = 0;
      Object.keys(this.form).forEach(element => {
        this.form[element] =
          typeof this.form[element] === "string"
            ? this.form[element].trim()
            : null;
        fieldValue = this.form[element];
        if (fieldValue !== null) {
          countFields++;
        }
      });

      if (countFields === Object.keys(this.form).length) {
        return true;
      }

      return false;
    },
    sendForm(e) {
      e.preventDefault();
      if (this.isFormValid()) {
        //Send data
        Utilities.request("send-form", "POST", JSON.stringify(this.form)).then(
          response => {
            this.form = {
              name: null,
              email: null,
              subject: null,
              comment: null
            };
            alert(response.message);
          }
        );
      }
    }
  }
};
</script>

<style lang="en" scope></style>
