<template lang="en">
    <div class="col md-6 lg-6 home__description--personal-information">
      <h4>Personal information</h4>
        <dl v-for="art in info">
            <dt>{{ art.name }}</dt>
            <dd>{{ art.info }}</dd>
        </dl>
    </div>
</template>

<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      info: [{}]
    };
  },
  methods: {
    getBasicInfo() {
      Utilities.sendRequest("info").then(information => {
        this.info = information;
      });
    }
  },
  created() {
    this.getBasicInfo();
  }
};
</script>

<style lang="en" scope></style>
