<template lang="en">
    <div class="col social-networks text-center">
        <ul>
            <li v-for="social in networks">
                <a :href="social.link" v-html="social.icon" rel="noreferrer" target="_blank">
                </a>
            </li>
        </ul>
    </div>
</template>

<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      networks: []
    };
  },
  methods: {
    getSocialnetworks() {
      Utilities.sendRequest("social-networks").then(social => {
        this.networks = social;
      });
    }
  },
  created() {
    this.getSocialnetworks();
  }
};
</script>
<style lang="en"></style>
