<template lang="en">
<div id="education" class="row section">
    <div class="col education">
      <h2>Education</h2>
      <ul>
          <li v-for="(education, index) in educationList">
            <div class="education__university" v-if="index === 0">
              <h3>{{ education.title }}</h3>
              <h4>{{ education.school }}</h4>
              <p>{{ education.date }}</p>
              <a :href="education.info" target="_blank" rel="noreferrer">{{education.info}}</a>
            </div>

            <div v-else>  
              <picture class="education__certification">
                <source :srcset="education.info.jp2" type="image/jp2"> <!-- format supported by safari -->
                <source :srcset="education.info.webp" type="image/webp"> <!-- format supported on many browsers -->
                <source :srcset="education.info.jpg" type="image/jpg"> <!-- common supported format -->
                <img :src="education.info.jpg" alt=""> <!-- default -->
              </picture>
            </div>
            
            <hr class="hr">
          </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      educationList: []
    };
  },
  methods: {
    getEducationInfo() {
      Utilities.sendRequest("education").then(education => {
        this.educationList = education;
      });
    }
  },
  created() {
    this.getEducationInfo();
  }
};
</script>

<style lang="en"></style>
