<template lang="en">
<div id="experience" class="row section">
  <div class="col timeline">
    <h2>Work experience</h2>
    <ol>
      <li v-for="experience in experiencesList">
        <div class="timeline__item">
          <h3>{{ experience.agency }} <small>{{ `${experience.start }${experience.exit}` }}</small></h3>
          <p>{{ experience.position }}</p>
          <p v-html="experience.responsabilities"></p>
        </div>
      </li>
    </ol>
  </div>
  </div>
</template>
<script>
import { Utilities } from "@/assets/js/utilities.js";

export default {
  data() {
    return {
      experiencesList: []
    };
  },
  methods: {
    getExperienceInfo() {
      Utilities.sendRequest("experiences").then(experiences => {
        this.experiencesList = experiences;
      });
    }
  },
  created() {
    this.getExperienceInfo();
  }
};
</script>
<style lang="en" scope></style>
